body {
  background-image: url(/static/media/background.4e0b448e8085812129ee.jpg);
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  font-family: Arial, sans-serif;
  height: 100vh;
  margin: 10px;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.app-container {
  background-color: #fff;
  border-radius: 16px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
  padding: 20px;
  max-width: 1600px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  color: tomato;
  font-family: "Cursive", cursive;
  font-size: 3em;
  margin-bottom: 30px;
  text-align: center;
}

h2 {
  color: #333;
  font-family: Arial, sans-serif;
  font-size: 2em;
  margin-bottom: 25px;
  text-align: center;
}

input[type="text"] {
  border: 2px solid #ccc;
  border-radius: 100px;
  font-size: 1em;
  padding: 12px 20px;
  width: calc(100% - 150px);
  transition: border-color 0.2s, box-shadow 0.2s;
  box-sizing: border-box;
}

input[type="text"]:focus {
  border-color: tomato;
  box-shadow: 0 0 8px #ff6347 0.15;
  outline: none;
}

button {
  background-color: tomato;
  border: none;
  border-radius: 10px;
  color: #fff;
  cursor: pointer;
  font-size: 1em;
  padding: 14px 25px;
  margin-left: 20px;
  transition: background-color 0.2s, transform 0.1s;
}

button:hover {
  background-color: #ff3b2e;
  transform: scale(1.05);
}

.new-task-controls {
  display: flex;
  align-items: center;
  width: 100%;
  margin-bottom: 25px;
  justify-content: center;
}

ul {
  list-style: none;
  width: 100%;
  padding: 0;
  margin-top: 35px;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

li {
  display: flex;
  align-items: center;
  background-color: #f9f9f9;
  border-radius: 12px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.07);
  padding: 15px 20px;
  transition: background-color 0.2s, transform 0.1s;
  width: 100%;
  box-sizing: border-box;
}

li:hover {
  background-color: #f1f1f1;
  transform: translateY(-2px);
}

li input[type="checkbox"] {
  cursor: pointer;
  margin-right: 20px;
  transform: scale(1.4);
}

li input[type="text"] {
  background: transparent;
  border: none;
  flex-grow: 1;
  font-size: 1.1em;
  outline: none;
  padding: 10px 15px;
  min-width: 200px;
  word-break: break-word;
}

.task-done {
  color: #999;
  font-style: italic;
  text-decoration: line-through;
}

li button.delete-btn {
  background: none;
  border: none;
  color: #c33;
  cursor: pointer;
  font-size: 1.4em;
  margin-left: 15px;
  transition: color 0.2s;
}

li button.delete-btn:hover {
  color: #a00;
}
